import React from 'react';
import { graphql } from 'gatsby';

import Hero from '../components/hero';
import Layout from '../components/layout';
import PageHead from '../components/page-head';
import Toc from '../components/toc';
import { siteFragment } from '../fragments/site';

import { getMenu } from '../utils/menu';

const NotFoundPage = ({ data, location }) => {
  const menu = getMenu(data.allDirectory.nodes);
  return (
    <Layout data={data} location={location} menu={menu}>
      <div className="main home-main">
        <Hero title="404 Not Found">
          What you are looking for is either no longer here or has moved.
        </Hero>
      </div>
      <Toc />
    </Layout>
  );
};

export default NotFoundPage;

export const Head = ({ data }) => {
  const title = `404: Not Found | ${data.site.siteMetadata.title}`;
  return <PageHead title={title} />;
};

export const pageQuery = graphql`
  query {
    allDirectory(filter: { relativeDirectory: { eq: "" } }) {
      nodes {
        base
        relativeDirectory
      }
    }
    site {
      ...SiteMetadata
    }
  }
`;
